 
.App{
  overflow: hidden;
  background-color:antiquewhite;
  height: 100vh;
}

 .AppChild {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: baseline;
  position : relative;
  padding: 1em;
  gap: 10px;
  flex-wrap: wrap;
  /* background-color: antiquewhite; */
}

.nevBar{
  display: flex;
  flex-wrap: wrap;
}


.button {
  padding: 7px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin: auto;
}

.button:hover {
  background-color: #45a049;
}

.button:active {
  background-color: #3e8e41;
}

.button:focus {
  outline: none;
  box-shadow: 0 0 5px rgba(81, 203, 238, 1);
}

.nevBar{
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

