/* SortingVisualizer.css */
.array-container-parent {
    display: flex;
    height: 39em; /* Adjust this to your desired height */
    width: 90%;
    overflow: hidden;
    margin: auto;
  }

  .array-container{
    display: flex;
    justify-content: center;
    /* align-items:stretch; */
    background-color: lightskyblue;
    width: 95%;
    margin: 0 auto;
    height: 90%;
    padding: 10px;
    /* right: 20% */
  }
  
  .array-bar {
    align-items: center;
    width: 10px; /* Adjust the width of the array bars */
    margin: 0 1px; /* Add spacing between array bars */
    background-color: #007bff; /* Blue color for the bars */
    transition: height 0.3s ease; /* Transition height changes */
  }
  